/* Import Tailwind CSS base styles */
@tailwind base;
@tailwind components;
@tailwind utilities;

/* Important: Set RTL direction for Arabic content */
body {
  direction: rtl;
}

/* Smooth transition effect for dark mode toggle */
.dark-transition {
  transition: all 0.5s ease-in-out;
}

/* Dark mode icon styling */
.dark-mode-icon {
  display: inline-block;
  transform-origin: center;
  transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Rotate icon on hover */
button:hover .dark-mode-icon {
  transform: rotate(360deg);
}

/* Scale down icon on click */
button:active .dark-mode-icon {
  transform: scale(0.9);
}

/* Important: Floating animation for buttons */
@keyframes floating {
  0% { transform: translateY(0px); }
  50% { transform: translateY(-4px); }
  100% { transform: translateY(0px); }
}

button:hover {
  animation: floating 2s ease-in-out infinite;
} 